import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import ElementUI  from 'element-ui'
import 'element-ui/lib/theme-default/index.css'

Vue.config.debug = true;
Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(ElementUI)

new Vue({
  el: '#app',
  render: h => h(App)
})



